@import url(https://fonts.googleapis.com/css2?family=Rampart+One&display=swap);
:root {
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

body {
  font-family: "Rampart One", cursive;
  color: #4e4e4e;
  color: var(--secondary);
  background-color: aliceblue;
}

.App {
  max-width: 960px;
  margin: 0 auto;
}
.title h1 {
  color: #efb6b2;
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}

.title h2,
.title p {
  text-align: center;
}
.title h2 {
  margin-top: 60px;
  font-size: 2.6rem;
}
form {
  font-size: 0.7rem;
}
#addFileGif {
  width: 100px;
}

.progress-bar {
  height: 5px;
  background: #efb6b2;
  background: var(--primary);
  margin-top: 20px;
}

.img-grid {
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}
.img-wrap {
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  position: relative;
  opacity: 0.8;
}
.img-wrap img {
  min-width: 100%;
  min-height: 100%;
  max-width: 150%;
  position: absolute;
  top: 0;
  left: 0;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(136, 182, 190, 0.8);
}
.backdrop img {
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(14, 14, 87, 0.5);
  border: 3px solid rgb(255, 242, 242);
}

